import React from 'react';

import './_sign-up.sass';

const SignUp = () => {
    document.title = "sign up";

    return (
        <div className="sign-up">
            <h2 className="sign-up-title">sign up</h2>
            <h2 className="username">UserName:</h2>
            <h2 className="password00">Password:</h2>
            <h2 className="password01">Repeat Password:</h2>

            <input type="text"     className="e-name"                  rows="1" cols="15"/>
            <input type="password" className="pass0" autoComplete="on" rows="1" cols="15"/>
            <input type="password" className="pass1" autoComplete="on" rows="1" cols="15"/>

            <button className="go-button">GO</button> 

            <a href="/" className="go-back-button">go back</a>

            {/* if error => alert("please repeat password correctly..."); */}
        </div>
    );
}

export default SignUp;