import React from 'react';
import { useParams } from 'react-router-dom';

import './_chat.sass';

import { getBasicData } from "./_getBasicData";

const chatRoomList = [
    "# room 1",
    "# room 2",
    "# room 3",
    "# room 4",
    "# room 5",
    "# room 6",
    "# room 7",
];

const voiceChatList = [
    "# voice 1",
    "# voice 2",
    "# voice 3",
];

const CategoriesJSX = () => {
    return (
        [
        chatRoomList.map(category => <li className="category"><a href="#">{category}</a></li>),
        <br />,
        voiceChatList.map(category => <li className="category"><a href="#">{category}</a></li>)
        ]
    )
};

const Categories = ({user}) => (
    <div className="categories">
        <h1 id="user">{user}</h1>
        <nav>
            <ul>
                <CategoriesJSX />
            </ul>
        </nav>
    </div>
)

const Messages = ({category, placeholder}) => (
    <div className="messages">
        <div id="top">
            <h1 id="category">{category}</h1>
            <h1 id="log_out"><a href="/">Log out</a></h1>
        </div>

        <div id="chat-screen">
            <div className="mmm">
                <h4 className="author">author</h4>
                <h4 className="message"></h4> {/* message */}
                <h4 className="date">date</h4>
            </div>

            {/* // % for message in messages %
            //     <script>
            //          append_message('message.username', `message.message`, 'message.date', "title.decode('utf-8')");
            //     </script>
            // % endfor % */}

            <h4 id="last"></h4>
            <script>
                {/* document.getElementById('last').scrollIntoView(); */}
            </script>
        </div>

        <div id="input">
            <textarea id="text-area" type="text" name="text-input" placeholder={placeholder} rows="10" cols="30"></textarea>
            <svg width="24" height="24" viewBox="0 0 24 24"> {/* onClick=appendFile */}
                <path
                fill="currentColor"
                d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm5 11h-4v4h-2v-4H7v-2h4V7h2v4h4v2z">
                </path>
            </svg>
        </div>
    </div>
)

const StatusBar = () => (
    <div className="status-bar"></div>
)

const Chat = () => {
    let { id, category } = useParams();
    let { userName, categoryStatus } = getBasicData(id, category);

    if (categoryStatus != "valid")
        category = "room #1";

    let placeholder = `Message #${category}`; // .decode('utf-8')

    return (
        <div className="chat">
            <Categories user={userName} />
            <Messages category={category} placeholder={placeholder} />
            <StatusBar />
        </div>
    );
}

export default Chat;